@import "../../../common/style/sass/variables";
@import "../../../common/style/sass/mixins";

.container {
  @include container();

  .header {
    @include text(48px, 700);
    text-align: center;
  }

  .namesTechnologies {
    @include flex(row, center, center, wrap);

    .nameTech {
      position: relative;
      padding: 37px;
      @include text(20px, 700);
      padding-bottom: 10px;
      border: none;
      background: transparent;

      &:focus {
        &:before {

          content: '';
          position: absolute;
          height: 2px;
          width: 100%;
          bottom: -10px;
          left: 0;
          background-color: $activeColor
        }
      }

    }
  }

